.nameComponent{
    display:flex;
    justify-content: center;
    align-content: center;
    flex-direction: column;
    padding-left: 2%;
    width: 70%;
    height:100%;
    border-right-width: 0.1em;
    border-right-color: gold;
    border-right-style: dotted;
    text-align: center
}

.bigName{
    font-family: 'DAYROM';
    font-size: 8em;
}

.smallName{
    font-family: 'DAYROM';
    font-size: 4em;
}

.nav{
    width: 30%;
    height: 100%;
    display:flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.navItemContainer{
    width:100%;
    height: 25%;
}

.navitem{
    width:100%;
    text-align: right;
    height: 100%;
    display:flex;
    align-items: center;
    justify-content:center;
    background: white;
    border-style: none;
    outline: none;
    cursor: pointer;
    transition-property: background, color; 
    transition-duration: 0.15s;
}


.navitem:hover{
    background:gold;
    color: white;
    
}

.blog{
    display: flex;
    justify-content: center;
    flex-direction: column;
}
.blog .title{
    font-family: Coolvetica;
    font-size: 4em;
    color: #1F1F1F
}

.blog .text{
    font-family: Coolvetica;
    font-size: 1.5em;
    color: #1F1F1F
}

.blog .date{
    font-family: Coolvetica;
    font-size: 1em;
    color: lightgrey;
}

.blog .picture{
    width: 100%;
    height: 20em;
    object-fit: cover;
}

@font-face {
    font-family: 'DAYROM';
    src: url('../fonts/DAYROM.woff') format('woff');
    font-weight: normal;
    font-style: normal;
  
}


@font-face {
    font-family: 'Coolvetica';
    src: url('../fonts/coolvetica rg.woff') format('woff');
    font-weight: normal;
    font-style: normal;
  
}